import React, { useState } from "react";
import { Link } from "react-router-dom";
function NavBar() {
  const [toggle, setToggle] = useState(false);

  const handleScroll = () => {
    if (window.scrollY > 20) {
      document.querySelector(".navbar").className = "navbar scroll";
    } else {
      document.querySelector(".navbar").className = "navbar";
    }
  };

  return (
    <nav
      className="flex fixed inset-x-0 top-0 items-center py-3 flex-wrap  navbar scroll z-10 animate__animated animate__bounceInLeft animate__delay-800ms"
      onScroll={handleScroll}
    >
      <img className="lg:w-32 w-24" src="/images/logo.png" alt="" />
      <button
        className="inline-flex p-2 border border-transparent  hover:border-custom-yellow rounded lg:hidden ml-auto "
        onClick={() => setToggle(!toggle)}
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>
      <div
        className={
          "w-full lg:inline-flex lg:flex-grow lg:w-auto " +
          (toggle ? " flex" : " hidden")
        }
      >
        <div className="lg:inline-flex lg:flex-row lg:ml-auto lg:items-center flex flex-col mt-3 ">
          <Link
            to="/"
            className="lg:inline-flex lg:w-auto px-3 mx-2 py-2 rounded uppercase hover:text-custom-yellow tracking-wide text-sm md:text-base lg:text-base transition duration-500 ease-in-out  transform  hover:scale-110"
            onClick={() => setToggle(!toggle)}
          >
            Home
          </Link>
          <Link
            to="/"
            className="lg:inline-flex lg:w-auto px-3 mx-2 py-2 rounded uppercase hover:text-custom-yellow tracking-wide text-sm md:text-base lg:text-base transition duration-500 ease-in-out  transform  hover:scale-110"
            onClick={() => setToggle(!toggle)}
          >
            About us
          </Link>
          <Link
            to="/"
            className="lg:inline-flex lg:w-auto px-3 mx-2 py-2 rounded uppercase hover:text-custom-yellow tracking-wide text-sm md:text-base lg:text-base transition duration-500 ease-in-out  transform  hover:scale-110 "
            onClick={() => setToggle(!toggle)}
          >
            Courses
          </Link>
          <Link
            to="/contact"
            className="lg:inline-flex lg:w-auto px-3 mx-2 py-2 rounded uppercase hover:text-custom-yellow tracking-wide text-sm md:text-base lg:text-base transition duration-500 ease-in-out  transform  hover:scale-110 "
            onClick={() => setToggle(!toggle)}
          >
            Contact
          </Link>
          <Link
            to="/contact"
            className="lg:inline-flex lg:w-auto px-3 py-2 rounded uppercase  lg:ml-3"
            onClick={() => setToggle(!toggle)}
          >
            <button className="uppercase bg-black text-white lg:p-2  p-2  rounded border border-transparent lg:hover:bg-white lg:hover:text-custom-yellow lg:hover:border-black font-normal tracking-wider text-sm lg:text-base transition duration-500 ease-in-out  transform  hover:scale-110">
              get free consultation
            </button>
          </Link>
        </div>
      </div>
    </nav>
  );
}

export default NavBar;
