import React from "react";
import Slider from "react-slick";
function Carousel(props) {
  var settings = {
    dots: true,
    infinite: true,
    speed: 300,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    autoplay: true,
    fade: true,
  };
  return (
    <div className="mt-10">
      <Slider {...settings} className="text-black">
        {props.data.map((data) => (
          <div className="p-3" key={Math.random()}>
            <div className="flex flex-col items-center text-center tracking-wider leading-normal">
              <p className="break-words lg:w-1/2 md:w-1/2 w-full">
                {data.message}
              </p>
              <img
                src={`./images/${data.img}`}
                alt="user-img"
                className="w-24 mt-5 inline-flex text-center
               items-center"
              />
              <p className="text-custom-blue mt-3">{data.userName}</p>
              <p>{data.userCity}</p>
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
}

export default Carousel;
